body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

center {
  margin: 0 auto;
  float: none;
  margin-bottom: 10px;
}
.middleCard {
  position: fixed;
  top: 0%;
  left: 0%;
  width: 100%;
  height: 0%;
}
.textAreaStyle {
  position: relative;
  border: 2px solid rgba(230, 230, 230, 0.75);
  border-radius: 40px;
  width: 600%;
}
.textborder {
  border: 2px solid rgba(230, 230, 230, 0.75);
  border-radius: 40px;
}
.btn-primary {
  font-family: Raleway-SemiBold;
  font-size: 13px;
  color: rgba(58, 133, 191, 0.75);
  letter-spacing: 1px;
  line-height: 15px;
  border: 2px solid rgba(58, 133, 191, 0.75);
  border-radius: 40px;
  background: transparent;
  -webkit-transition: all 0.3s ease 0s;
  transition: all 0.3s ease 0s;
}
.btn-secondary {
  font-family: Raleway-SemiBold;
  font-size: 13px;
  color: rgba(24, 24, 24, 0.623);
  letter-spacing: 1px;
  line-height: 15px;

  border-radius: 40px;
  background: transparent;
  -webkit-transition: all 0.3s ease 0s;
  transition: all 0.3s ease 0s;
}
.btn-success {
  font-family: Raleway-SemiBold;
  font-size: 13px;
  color: rgba(38, 129, 38, 0.75);
  letter-spacing: 1px;
  line-height: 15px;
  border: 2px solid rgba(38, 129, 38, 0.75);
  border-radius: 40px;
  background: transparent;
  -webkit-transition: all 0.3s ease 0s;
  transition: all 0.3s ease 0s;
}

.customtitle {
  background-color: rgba(200, 200, 200, 0.75);
  padding: 1%;
}
.sendBtn {
  font-family: Raleway-SemiBold;
  font-size: 50px;
  color: rgba(38, 129, 38, 0.75);
  border: 2px solid rgba(38, 129, 38, 0.75);
  border-radius: 50px;
  width: 50px;
  height: 50px;
  background: transparent;
  -webkit-transition: all 0.3s ease 0s;
  transition: all 0.3s ease 0s;
}
.customcard {
  letter-spacing: 1px;
  line-height: 15px;
  border-radius: 10px;
  background: transparent;
}
.customcardheader {
  letter-spacing: 1px;
  line-height: 15px;
  border-radius: 40px;
  background-color: secondary;
}
.customcardbody {
  background-color: rgba(250, 250, 250, 0.5);
  border: 2px solid rgb(212, 212, 212);
}
.recvmsg {
  background-color: rgb(2, 107, 226);
  color: rgb(254, 254, 255);
  border: 2px solid rgb(2, 107, 226);
  border-radius: 20px;
  padding: 4%;
  padding-left: 10%;
  padding-right: 10%;
}
.sendmsg {
  background-color: rgb(21, 226, 2);
  color: rgb(254, 254, 255);
  border: 2px solid rgb(28, 226, 2);
  border-radius: 20px;
  padding: 4%;
  padding-left: 10%;
  padding-right: 10%;
}

